/*64 + 58 + 48 + 48 + 52vh + 4px du bas du canvas :*/

canvas {
    width : 100vw;
    height : 46vh;
    outline : none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    background-color : white;
    cursor : pointer;
}

.content {
    height : calc(54vh - 218px);
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction : column;
        flex-direction : column;
    -ms-flex-pack : center;
        justify-content : center;
}

a {
    text-decoration : none;
}

.titleDrawer {
    height : 10vh;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-pack : center;
        justify-content : center;
    -ms-flex-align : center;
        align-items : center;
    background-color : rgb(0, 188, 212);
    color : white;
    font-size : x-large;
    font-style: italic;
}

/*Thanks to this property, no margin-bottom after the canvas*/
.surroundingCanvas {
    height : 46vh;
    background-image : url("img/gesture.png");
}

/* HOME */

.home_titre {
    background-color : #007ac1;
    height : 23vh;
    width : 100vw;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-pack : center;
        justify-content : center;
    -ms-flex-align : center;
        align-items : center;
    color : white;
    font-size : xx-large;
}

.home_menu {
    background-color : #b2fab4;
    height : 70vh;
    width : 100vw;
    display : -ms-flexbox;
    display : flex;
    -ms-flex-direction : column;
        flex-direction : column;
    -ms-flex-align : center;
        align-items : center;
    -ms-flex-pack : distribute;
        justify-content : space-around;
}

.home_liens a {
    text-decoration : none;
    color: black;
    text-transform : capitalize;
    padding : 5vw 10vw ;
    background-color : #03a9f4;
    border-radius: 2px;
    border : 1px solid #519657;
    -webkit-box-shadow:2px 2px 4px #519657;
            box-shadow:2px 2px 4px #519657;
}

.home_liens a:visited {
    color: black;
}

